import React, { Component } from "react";
import "../assets/css/style.css";
import {
  Modal,
  Button,
  OverlayTrigger,
  Popover,
  Tooltip
} from "react-bootstrap";

class RatingStars extends Component {
  state = {
    starCount: 5,
    txid: 3
  };

  onHandleRating = (id, k, b) => {
    console.log("obj is " + b.id)
    // alert(id, b.id, b.recipient);
    alert("transcation id is " + b.id);
    alert("transcation recipient is " + b.recipient);

  };

  render() {
    console.log("key" + this.state.txid);
    return (
      <span key={this.state.txid} className="rating">
        {[1, 2, 3, 4, 5].map((o, i) => {
          console.log(o);
          // console.log("kkkkk");

          return (
            <RatingStar
              key={i}
              rating={o}
              txid={this.props.belongsTo.id}
              // onClick={() => this.props.onHandleRating(o)}
              onRate={() =>
                this.onHandleRating(i, this.props.txid, this.props.belongsTo)
              }
            />
          );
        })}
      </span>
    );
  }
}

