class RatingStar extends Component {
  state = {};
  render() {
    console.log("here111");
    return [
      <input
        className="required rating-input"
        // key={`${this.props.rating}`}
        id={"rating-input-" + this.props.txid + "-" + this.props.rating}
        name={"rating-input-" + this.props.txid}
        // key={3}
        type="radio"
        value="5 Stars"
        // onClick={() => {
        //   console.log("here");
        //   this.props.onHandleR;
        // }}
      />,
      <label
        key={`${this.props.rating}`}
        for={"rating-input-" + this.props.txid + "-" + this.props.rating}
        className="rating-star"
        onClick={this.props.onRate} // this onClick event fires off onRate property in line 35, which then call onHandleRating on RatingStars obj
      />
    ];
  }
}

